.dashboard {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-rows: 4rem 1fr;

  background-color: var(--second-grey);
}

.dashboard > * {
  height: 100%;
  width: 100%;
}

.dashboard > .dashboard__title {
  padding: 0 16px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.dashboard > .dashboard__body {
  padding: 0 16px;

  /* display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center; */
}

.dashboard > .dashboard__body.debug > *:hover {
  border: 1px solid var(--debug);
}

.dashboard__body > .metrics__row {
  margin-bottom: 16px;
  height: 6rem;
  width: 100%;
  min-width: 100%;

  display: grid;
  grid-template-columns: 20% 6rem 1fr;
  grid-column-gap: 16px;
}

.metrics__row > .storage__metrics {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-columns: 1fr 3px 1fr;

  background-color: var(--first-grey);
  border-radius: 8px;
  box-shadow: 0 10px 18px rgb(0 0 0 / 20%);
}

.storage__metrics > .storage__metric {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-rows: 60% 40%;
}

.storage__metric > * {
  display: flex;
  align-items: center;
  justify-content: center;
}

.storage__metric > *:first-of-type {
  color: var(--third-grey);
  font-size: 2rem;
}

.storage__metrics > .storage__metrics__divider {
  height: 100%;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.storage__metrics__divider > .divider__bar {
  height: 90%;
  width: 1px;

  background-color: var(--second-grey);
}

.metrics__row > .add__table {
  position: relative;

  height: 100%;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: var(--first-grey);
  border-radius: 8px;
  box-shadow: 0 10px 18px rgb(0 0 0 / 20%);
}

.add__table:hover::after {
  padding: 4px 8px;

  height: 1.5rem;
  width: 5rem;

  display: flex;
  align-items: center;
  justify-content: center;

  z-index: 5;

  content: attr(data-title);
  position: absolute;
  bottom: calc(-1 * (2rem + 8px));
  left: 0;
  background-color: var(--first-grey);
  color: var(--third-grey);
  box-shadow: 0 10px 18px rgb(0 0 0 / 20%);
  border-radius: 4px;
  transition: bottom ease-in-out 0.3s;
}

.add__table > * {
  height: 100%;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.request__metrics {
  height: 10rem;
  width: 100%;
  min-width: 100%;
}

.request__metrics > .request__amount {
  padding: 8px;

  height: 100%;
  width: 16rem;

  background-color: var(--first-grey);
  border-radius: 8px;
  box-shadow: var(--shadow);
}

.request__metrics > .request__amount > canvas {
  height: calc(8rem - 16px);
  width: 100%;
}

@media only screen and (max-width: 1300px) {
  .dashboard__body > .metrics__row {
    grid-template-columns: 30% 6rem 1fr;
  }
}
