.app__header {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-columns: repeat(3, 1fr);

  background-color: var(--primary);
  color: var(--first-grey);
}

.app__header > * {
  height: 100%;
  width: 100%;
}

.app__header > .header__left {
  padding: 0 4px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.app__header > .header__center {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
