.addTable {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-rows: 4rem 1fr;
}

.addTable__header {
  padding: 16px;

  height: 100%;
  width: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  background-color: #f5f7f9;
}

.addTable__body {
  display: grid;
  grid-template-columns: 12rem 1fr;
}

.addTable__body > .addTable__sidebar {
  height: 100%;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  background-color: #f5f7f9;
  color: #9daab6;
}

.addTable__sidebar > .addTable__sidebar__header {
  padding: 16px;

  height: 2rem;
  width: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.addTable__sidebar > .addTable__sidebar__item {
  /* padding-left: 16px;  */
  height: auto;
  min-height: 2rem;
  width: 100%;

  display: grid;
  grid-template-rows: 2rem auto;

  color: #9daab6;
}

.addTable__sidebar__item > span {
  /* padding: 16px; */
  margin: 0 16px;

  height: 100%;
  width: calc(100% - 32px);

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  border-radius: 4px;
}

.addTable__sidebar__item > span:hover {
  background-color: var(--second-grey);
}

.addTable__sidebar__item > .addTable__sidebar__item__specs {
  padding-left: 16px;
  height: auto;
  width: 100%;

  display: grid;
  grid-template-columns: 10px 1fr;
}

.addTable__sidebar__item__specs > .item__specs__line {
  height: 100%;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.item__specs__line > div {
  height: 90%;
  width: 2px;
  background-color: var(--third-grey);
}

.item__specs__container {
  padding-left: 4px;
}

.addTable__body > .addTable__form {
  background-color: var(--third-grey);
}
