@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code&display=swap);
:root {
  --primary: rgb(86, 88, 214);
  --primary-transparent: rgba(86, 88, 214, 0.4);
  --error: rgb(255, 0, 51);
  --error-transparent: rgb(255, 0, 51, 0.4);
  --debug: rgb(255, 105, 180);
  --first-grey: rgb(252, 252, 252);
  --second-grey: rgb(237, 237, 237);
  --third-grey: rgb(209, 209, 209);
  --shadow: 0 10px 18px rgb(0 0 0 / 20%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.app {
  height: 100vh;
  width: 100vw;

  display: grid;
  grid-template-rows: 2rem 1fr;
}

.app__body {
  height: 100%;
  width: 100%;

  background-color: rgb(237, 237, 237);

  background-color: var(--second-grey);
}

.login {
  height: 100vh;
  width: 100vw;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  background-color: rgb(237, 237, 237);

  background-color: var(--second-grey);
}

.login > form {
  height: 40%;
  width: 30%;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  background-color: rgb(252, 252, 252);

  background-color: var(--first-grey);
  border-radius: 8px;
}

.login > form > input {
  margin-bottom: 8px;
  padding: 0 8px;

  height: 2rem;
  width: 80%;

  color: rgb(209, 209, 209);

  color: var(--third-grey);
  background-color: rgb(237, 237, 237);
  background-color: var(--second-grey);
  border: 2px solid rgb(237, 237, 237);
  border: 2px solid var(--second-grey);
  border-radius: 4px;
  outline: none;

  transition: border ease-in-out 0.3s;
}

.login > form > input:focus {
  border: 2px solid rgb(86, 88, 214);
  border: 2px solid var(--primary);
}

.login > form > input[type='submit'] {
  padding: 0;
  color: rgb(252, 252, 252);
  color: var(--first-grey);
  background-color: rgb(86, 88, 214);
  background-color: var(--primary);
  border: 2px solid rgb(86, 88, 214);
  border: 2px solid var(--primary);
}

.app__header {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-columns: repeat(3, 1fr);

  background-color: var(--primary);
  color: var(--first-grey);
}

.app__header > * {
  height: 100%;
  width: 100%;
}

.app__header > .header__left {
  padding: 0 4px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.app__header > .header__center {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.dashboard {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-rows: 4rem 1fr;

  background-color: var(--second-grey);
}

.dashboard > * {
  height: 100%;
  width: 100%;
}

.dashboard > .dashboard__title {
  padding: 0 16px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.dashboard > .dashboard__body {
  padding: 0 16px;

  /* display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center; */
}

.dashboard > .dashboard__body.debug > *:hover {
  border: 1px solid var(--debug);
}

.dashboard__body > .metrics__row {
  margin-bottom: 16px;
  height: 6rem;
  width: 100%;
  min-width: 100%;

  display: grid;
  grid-template-columns: 20% 6rem 1fr;
  grid-column-gap: 16px;
}

.metrics__row > .storage__metrics {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-columns: 1fr 3px 1fr;

  background-color: var(--first-grey);
  border-radius: 8px;
  box-shadow: 0 10px 18px rgb(0 0 0 / 20%);
}

.storage__metrics > .storage__metric {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-rows: 60% 40%;
}

.storage__metric > * {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.storage__metric > *:first-of-type {
  color: var(--third-grey);
  font-size: 2rem;
}

.storage__metrics > .storage__metrics__divider {
  height: 100%;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.storage__metrics__divider > .divider__bar {
  height: 90%;
  width: 1px;

  background-color: var(--second-grey);
}

.metrics__row > .add__table {
  position: relative;

  height: 100%;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  background-color: var(--first-grey);
  border-radius: 8px;
  box-shadow: 0 10px 18px rgb(0 0 0 / 20%);
}

.add__table:hover::after {
  padding: 4px 8px;

  height: 1.5rem;
  width: 5rem;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  z-index: 5;

  content: attr(data-title);
  position: absolute;
  bottom: calc(-1 * (2rem + 8px));
  left: 0;
  background-color: var(--first-grey);
  color: var(--third-grey);
  box-shadow: 0 10px 18px rgb(0 0 0 / 20%);
  border-radius: 4px;
  transition: bottom ease-in-out 0.3s;
}

.add__table > * {
  height: 100%;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.request__metrics {
  height: 10rem;
  width: 100%;
  min-width: 100%;
}

.request__metrics > .request__amount {
  padding: 8px;

  height: 100%;
  width: 16rem;

  background-color: var(--first-grey);
  border-radius: 8px;
  box-shadow: var(--shadow);
}

.request__metrics > .request__amount > canvas {
  height: calc(8rem - 16px);
  width: 100%;
}

@media only screen and (max-width: 1300px) {
  .dashboard__body > .metrics__row {
    grid-template-columns: 30% 6rem 1fr;
  }
}

