.addTable {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-rows: 4rem 1fr;
}

.addTable__header {
  padding: 16px;

  height: 100%;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;

  background-color: #f5f7f9;
}

.addTable__body {
  display: grid;
  grid-template-columns: 12rem 1fr;
}

.addTable__body > .addTable__sidebar {
  height: 100%;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;

  background-color: #f5f7f9;
  color: #9daab6;
}

.addTable__sidebar > .addTable__sidebar__header {
  padding: 16px;

  height: 2rem;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.addTable__sidebar > .addTable__sidebar__item {
  /* padding-left: 16px;  */
  height: auto;
  min-height: 2rem;
  width: 100%;

  display: grid;
  grid-template-rows: 2rem auto;

  color: #9daab6;
}

.addTable__sidebar__item > span {
  /* padding: 16px; */
  margin: 0 16px;

  height: 100%;
  width: calc(100% - 32px);

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;

  border-radius: 4px;
}

.addTable__sidebar__item > span:hover {
  background-color: var(--second-grey);
}

.addTable__sidebar__item > .addTable__sidebar__item__specs {
  padding-left: 16px;
  height: auto;
  width: 100%;

  display: grid;
  grid-template-columns: 10px 1fr;
}

.addTable__sidebar__item__specs > .item__specs__line {
  height: 100%;
  width: 100%;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.item__specs__line > div {
  height: 90%;
  width: 2px;
  background-color: var(--third-grey);
}

.item__specs__container {
  padding-left: 4px;
}

.addTable__body > .addTable__form {
  background-color: var(--third-grey);
}

.app__body {
  height: 100%;
  width: 100%;

  display: grid;
  grid-template-columns: 4rem 1fr;
}

.app__body > * {
  height: 100%;
  width: 100%;
}

.app__body > .body__sidebar {
  padding: 4px 0;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;

  background-color: var(--first-grey);
}

.body__sidebar a {
  color: #000;
}

.body__sidebar a.sidebar__item__active .sidebar__item {
  color: var(--primary);
  background-color: var(--primary-transparent);
}

.body__sidebar .sidebar__item {
  margin-bottom: 4px;

  height: 3.4rem;
  width: 3.4rem;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  border-radius: 8px;

  transition: background-color ease-in-out 0.3s;
}

.body__sidebar .sidebar__item:hover {
  background-color: var(--third-grey);
}

